import React, { Component } from 'react';
import styled from "styled-components";
import SkillPlatformBox from './SkillPlatformBox.js';
import C from './img/c.png';
import Cshop from './img/cshop.png';
import Java from './img/java.png';
import Python from './img/python.png';
import Html from './img/html.png';
import Css from './img/css.png';
import Js from './img/js.png';
import Mysql from './img/mysql.png';
import Oracle from './img/oracle.png';
import Androidstudio from './img/androidstudio.png';
import Reactt from './img/react.png';
import Arduino from './img/arduino.png';

import tmp from '../Skill/binary.jpg';

const SkillStyle = styled.div`
    width: 100%;
    height: 100%;
    position: relative;
    background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),url(${tmp});
    background-attachment: fixed;
    background-size: cover;  
`;

const ItemBoxStyle = styled.div `
    width: 60%;
    height: 100%;
    transition: 1s;
    @media (min-width:0px) and (max-width:680.9px) {
        width: 100%;
        margin-left: 0px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-left: 20.3%;
    }
`;

const Flex = styled.div `
    @media (min-width: 874.9px) {
        width: 100%;
        display: flex;
    }
`;

class SkillItemBox extends Component {
    Platform1= [{
        Smargin: "0px",
        Title:'Language',
        Border: "1vw",
        Height: "400px",
        Lmargin: "-15px",
        Img1: <img src={C} alt='' />,
        Img2: <img src={Cshop} alt='' />,
        Img3: <img src={Java} alt='' />,
        Img4: <img src={Python} alt='' />,
        Color1: "green",
        Color2: "blue",
        Color3: "red",
        Color3: "red"
    },{
        Smargin: "15px",
        Title:'Web Skills',
        Border: "0px",
        Height: "265px",
        Lmargin: "-15px",
        Img1: <img src={Html} alt='' />,
        Img2: <img src={Css} alt='' />,
        Img3: <img src={Js} alt='' />,
        Color1: "green",
        Color2: "blue",
        Color3: "blue",
    }
]

    Platform2= [{
        Smargin: "0px",
        Title:'Database',
        Border: "1vw",
        Height: "265px",
        Lmargin: "0px",
        Img1: <img src={Mysql} alt='' />,
        Img2: <img src={Oracle} alt='' />,
        Color1: "green",
        Color2: "green",
    },{
        Smargin: "15px",
        Title:'IDE & Library',
        Border: "0px",
        Height: "265px",
        Lmargin: "-15px",
        Img1: <img src={Androidstudio} alt='' />,
        Img2: <img src={Reactt} alt='' />,
        Img3: <img src={Arduino} alt='' />,
        Color1: "green",
        Color2: "green",
        Color3: "blue",
    }
]

renderPlatforms1 = (arr) => {
    return arr.map(a=> {
        return <SkillPlatformBox Smargin={a.Smargin} Title={a.Title} Border={a.Border}  Height = {a.Height} Lmargin = {a.Lmargin} 
        Img1={a.Img1} Img2={a.Img2} Img3={a.Img3} Img4={a.Img4} Img5={a.Img5} Img6={a.Img6}
        Color1={a.Color1} Color2={a.Color2} Color3={a.Color3} Color4={a.Color4} key={a.Title} />
    })
}

renderPlatforms2 = (arr) => {
    return arr.map(a=> {
        return <SkillPlatformBox Smargin={a.Smargin} Title={a.Title} Border={a.Border} Height = {a.Height} Lmargin = {a.Lmargin} 
        Img1={a.Img1} Img2={a.Img2} Img3={a.Img3} 
        Color1={a.Color1} Color2={a.Color2} Color3={a.Color3} key={a.Title}
        />
    })
}
    render() {
        return (
        <div>
            <SkillStyle>
                <br></br>
            <ItemBoxStyle>
                <Flex>
                    {this.renderPlatforms1(this.Platform1)}
                </Flex>
                <Flex>
                    {this.renderPlatforms2(this.Platform2)}
                </Flex>
            </ItemBoxStyle>
                <br></br>
            </SkillStyle>
        </div>
        );
    }
}

export default SkillItemBox;