import React, { Component } from 'react';
import styled from "styled-components";


const PlatformStyle = styled.div`
    width: 29.8vw;
    height: ${props => props.Height};
    background-color: white;
    margin-left: ${props => props.Smargin};
    border: 4px solid white;


    @media (min-width:0px) and (max-width:680.9px) {
        width: 100%;
        margin-left: 0px;
    }


    @media (min-width:681px) and (max-width:874.9px) {
        width: 75vw;
        margin-left: 0px;
    }
    

    @media (min-width:875px) and (max-width:1023.9px) {
        width: 40vw;
    }

    @media (min-width:1024px) and (max-width:1230.9px) {
        width: 34vw;
    }

    @media (min-width:1231px) {
    }

`;

const PlatformTitle = styled.div `
    width: 100%;
    height: 60px;
    margin-top: -22px;
    color: #eeeeee  ;
    font-size: 30px;
    text-align: center;
    line-height: 50%;
    font-family: 'Inter', sans-serif;
    background-color: #0032E6;
`;

const Img = styled.div `
    height: 100px;
    border-radius: 25px;    
    border-style: solid 5px;
    background-color: white;
    cursor: pointer;
    box-shadow: 0 2px 1px rgba(0,0,0,.1), 0 3px 6px rgba(1,0,0,.1);
    transition: all 0.3s;
    &:hover {
        transform: scale(1.1);
    }

    /* 마우스 오버(마우스 올렸을때) */
    &:hover{
        border-style: solid;
        border-width: 3px;
        border-color: ${props => props.Color};
        background-color: ${props => props.Color};
    }
    
    /* 마우스 클릭하고있을때 */
    &:active{
        border-style: solid;
        border-width: 3px;
        border-color: ${props => props.Color};
        background-color: ${props => props.Color};
    }

    overflow: hidden; 

`;




const Center = styled.div`
    width: 100%;
    margin-top: 50px;
    padding: 0px auto;
    text-align: center;
    display: flex;
    justify-content: center; align-items: center;
    
    @media (min-width:0px) and (max-width:324.9px) {
        margin-left: 5px;
    }
`;


class SkillPlatformBox extends Component {
    render() {
        return (
        <div>
            <PlatformStyle Smargin={this.props.Smargin} Border={this.props.Border} Height={this.props.Height} 
            Bradius1={this.props.Bradius1} Bradius2={this.props.Bradius2} Bradius3={this.props.Bradius3} Bradius4={this.props.Bradius4}>
                <br></br>
                <PlatformTitle style={{lineHeight: "60px"}}>
                
                    {this.props.Title}
                
                </PlatformTitle>
                
                <Center Lmargin={this.props.Lmargin}>
                    <div><Img Color={this.props.Color1}>{this.props.Img1}</Img></div>&nbsp;&nbsp;&nbsp;&nbsp;
                    <div><Img Color={this.props.Color2}>{this.props.Img2}</Img></div>&nbsp;&nbsp;&nbsp;&nbsp;
                    <div><Img Color={this.props.Color3}>{this.props.Img3}</Img></div>
                </Center>
                <Center Lmargin={this.props.Lmargin}>
                    &nbsp;<Img Color={this.props.Color3}>{this.props.Img4}</Img>
                </Center>

            </PlatformStyle>
            <br></br>
        </div>
        );
    }
}
export default SkillPlatformBox;
