import React, { Component } from 'react';
import styled from "styled-components";
import LevelItem from './LevelItem.js';

const LevelStyle = styled.div `
    width: 100%;
    font-family: 'Noto Sans KR', sans-serif;
    display: flex;
`;

const Red = styled.div`
    color: red;
`

const Green = styled.div`
    color: green;
`

const Blue = styled.div`
    color: blue;
`

class LevelBox extends Component {
    level= [{
        text:'■ Lv3',
        fcolor:'red'
        },{
        text:'■ Lv2',
        fcolor:'green'
        },{
        text:'■ Lv1',
        fcolor:'blue'
        }
    ]

    renderLevel = (arr) => {
        return arr.map(a=> {
            return <LevelItem text={a.text} fcolor={a.fcolor}/>
        })
    }

    render() {
        return (
        <div>
            <LevelStyle>
                <Blue>■ Lv1</Blue>&nbsp;&nbsp;&nbsp;&nbsp;<Green>■ Lv2</Green>&nbsp;&nbsp;&nbsp;&nbsp;<Red>■ Lv3</Red>
            </LevelStyle>
        </div>
        );
    }
}

export default LevelBox;        